.chat {
    align-items: stretch;
    height: 100vh;
    width: 100vw;
    position: absolute;
    top: 0;
    left: 0;
}

.chat-screen {
    flex-direction: column;
    flex-basis: 0;
    flex-grow: 4;
}

.chat-screen-messages {
    flex-direction: column;
    padding: 0 49px 0 86px;
    overflow-y: scroll;
}
